/*global $ */

import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Script from './Script';

class Acts extends Component {
  render() {
    let acts = [];

    for (var key in this.props.acts) {
      if (this.props.acts.hasOwnProperty(key)) {
        acts.push(
          <li key={key} >
            { key }
            <ul>
              <Scenes viewFn={this.props.viewFn} scenes={ this.props.acts[key].pActScenes } />
            </ul>
          </li>);
      }
    }

    return (
      <ul>
        { acts }
      </ul>
    );
  }
}

class Scenes extends Component {
  render() {
    let scenes = [];

    for (var key in this.props.scenes) {
      if (this.props.scenes.hasOwnProperty(key)) {
        scenes.push(
          <li key={key} >
            { key }
            <Contents viewFn={this.props.viewFn} contents={this.props.scenes[key].pMarkers} />
          </li>
        );
      }
    }

    return (
      <ul>
        { scenes }
      </ul>
    );
  }
}

class Contents extends Component {

  render() {
    let contents = [];
    let last = [null, null];

    for (var key in this.props.contents) {
      if (this.props.contents.hasOwnProperty(key)) {
        let marker = this.props.contents[key].pMarker;
        let elems = []

        switch (marker.tag) {
          case "Visual":
            last[1] = "Visual";
            elems = [
              <td>{marker.mChar.charName}</td>,
              <td>{marker.mAction}</td>
            ];
            break;
          case "Line":
            if (last[0] === marker.mChar.charName && last[1] === "Line") {
              elems = [
                <td></td>,
                <td>{marker.mLine}</td>
              ];
            } else {
              elems = [
                <td>{marker.mChar.charName}</td>,
                <td>{marker.mLine}</td>
              ];
            }

            last = [marker.mChar.charName, "Line"]
            break;
          default:
            elems = []
        }

        let cues = this.props.contents[key].pCues
        let css = ''

        if (cues.length) {
          css += 'highlight';
        }

        contents.push(
          <tr onMouseEnter={() => this.props.viewFn(cues)} className={css} key={key} >
            <td>{ key }</td>
            { elems }
          </tr>
        );
      }
    }

    return (
      <table>
        { contents }
      </table>
    );
  }
}

class CodeingArea extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 'Write your code here!!!'
    };

    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(event) {
    this.setState({value: event.target.value});
    this.props.submitCodeFn(event.target.value);
  }

  render() {
    return (
      <textarea rows={15} cols={80} value={this.state.value} onChange={this.handleChange} />
    );
  }
}

class CueViewer extends Component {
  render() {
    let cues = [];
    for (var i = 0; i < this.props.cues.length; i++) {
      cues.push(
        <li>{this.props.cues[i].department.deptName}</li>
      );
    }

    return <ul>{cues}</ul>
  }
}

class App extends Component {
  constructor(props) {
    super(props);
    this.setState = this.setState.bind(this);
    this._viewCues = this._viewCues.bind(this);
    this._newCode = this._newCode.bind(this);
    this.state = { cues: [], script : Script, errors : '' }
  }

  _newCode(c) {
    console.log(c);
    $.post("http://localhost:3000/process", c, (d) => d.hasOwnProperty('errors') ? this.setState({errors : d.errors}) : this.setState({script : d, errors: 'No errors!'}), 'json')
  }

  _viewCues(s) {
    this.setState({cues: s})
  }

  render() {
    return (
      <div className="App">
        <div className="App-header">
          <h2>{ Script.pTitle }</h2>
          <div style={{height: '200px', display: 'flex'}}>
            <div style={{ width: '300px'}}>
              <CueViewer cues={this.state.cues} />
            </div>
            <div>
              <CodeingArea submitCodeFn={this._newCode} />
            </div>
            <pre>
            {this.state.errors.split("\n").map(function(item) {
              return (
                <span>
                  {item}
                  <br/>
                </span>
              )
              })}
            </pre>
          </div>
        </div>
        <div style={{ height: '100vh', overflow: 'scroll'}}>
          <Acts viewFn={this._viewCues} acts={this.state.script.pActs} />
        </div>
      </div>
    );
  }
}

export default App;



